import { styled } from "styled-components";
import { careData } from "../../api";
import { useEffect, useState } from "react";
import { useParams } from "react-router-dom";
import { mainStyle } from "../../GlobalStyled";

//  
const Container = styled.div`
  margin: 160px auto;
  padding: 20px calc(${mainStyle.pcPadding}*2);
  width: 120%;
  font-family: "Jua", serif;

  @media screen and (max-width:600px){
    width: 100%;
    padding: 20px ${mainStyle.moPadding};
  }
`;

const Wrap = styled.div`
  margin-top: 20px;
`;
const Title = styled.h2`
  font-size: 50px;
  padding: 10px;
  margin-bottom: 10px;
  text-align: center;
  width: 600px;
  height: 60px;
  background-color: #007bff;
  color: white;
  border-radius: 10px;

  @media screen and (max-width:600px){
    font-size: 24px;
    width: 300px;
    height: 40px;
  }
`;

const Info = styled.div`
  line-height: 1.8;
  margin-bottom: 20px;
  margin-top: 30px;

  span {
    font-weight: 400;
  }

  p {
    margin-top: 20px;
    font-size: 30px;
  }

  p:last-child {
    font-size: 30px;
    color: #0c224e;
  }

  a {
    color: #007bff;
    text-decoration: none;

    &:hover {
      text-decoration: underline;
    }
  }

  @media screen and (max-width:600px){
    margin:0 auto;
   p{
    font-size: 18px;
   } 

   p:last-child{
    font-size: 18px;
   }
  }
`;

const Detail = () => {
  const { id } = useParams(); // URL    ID
  const [center, setCenter] = useState(null); //    

  // API  
  useEffect(() => {
    const fetchCenters = async () => {
      try {
        const data = await careData(); // API 
        const items = data?.response?.body?.items?.item || []; //  
        const selectedCenter = items.find((item) => item.cnterNm === id); //  ID  
        setCenter(selectedCenter);
      } catch (error) {
        console.error("   :", error);
      }
    };

    fetchCenters();
  }, [id]);

  if (!center) {
    return <Container>  ...</Container>;
  }

  // URL   
  const formatUrl = (url) => {
    if (!url) return null; // URL  null 
    if (!url.startsWith("http://") && !url.startsWith("https://")) {
      return `http://${url}`; // http://  
    }
    return url; //  URL 
  };

  return (
    <Container>
      <Wrap>
        <Title>#{center.cnterNm}</Title>
        <Info>
          <p>
            <span># : </span>
            {center.cnterChNm || " "}
          </p>
          <p>
            <span># : </span>
            {center.lotnoAddr || " "}
          </p>
          <p>
            <span># : </span>
            {center.rprsTelno || " "}
          </p>
          <p>
            <span>#  : </span>
            {center.sprtCnt || " "}
          </p>
          <p>
            <span># : </span>{" "}
            {center.hmpgAddr ? (
              <a
                href={formatUrl(center.hmpgAddr)} // URL   
                target="_blank"
                rel="noopener noreferrer"
              >
                {center.hmpgAddr}
              </a>
            ) : (
              " "
            )}
          </p>
        </Info>
      </Wrap>
    </Container>
  );
};

export default Detail;
