import { useForm } from "react-hook-form";
import styled from "styled-components";
import { careData } from "../../api"; // careData API 
import { useState } from "react";
import { mainStyle } from "../../GlobalStyled";
import { Link } from "react-router-dom";

const Wrap = styled.section`
  padding: 20px ${mainStyle.pcPadding};
  background-color: #f8f9fa;
`;

const Form = styled.form`
  display: flex;
  justify-content: center;
  margin-bottom: 20px;

  input {
    all: unset;
    width: 80%;
    height: 60px;
    border-bottom: 3px solid #0C224E;
    padding: 0 20px;
    box-sizing: border-box;
    font-size: 16px;

    &::placeholder {
      font-size: 16px;
      color: #aaa;
    }
  }
`;

const ConWrap = styled.div`
  margin-top: 60px;
  display: grid;
  grid-template-columns: repeat(2, 1fr); /* 3  */
  gap: 15px;
  padding: 0 10px;
  margin-bottom: 60px;

  @media (max-width: 768px) {
    grid-template-columns: repeat(2, 1fr); /*  2 */
  }

  @media (max-width: 480px) {
    grid-template-columns: 1fr; /*  1 */
  }
`;

const Con = styled.div`
  background-color: #007bff;
  height: 60px;
  color: white;
  text-align: center;
  border-radius: 25px;
  padding: 15px;
  font-size: 16px;
  font-weight: bold;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  transition: background-color 0.3s ease;

  &:hover {
    background-color: #0056b3; /*     */
  }
`;

const NoResultMessage = styled.div`
  font-size: 18px;
  color: #6c757d;
  text-align: center;
  margin-top: 50px;
  margin-bottom: 50px;
`;

const Search = () => {
  const {
    register,
    handleSubmit,
    formState: { errors },
  } = useForm();
  const [term, setTerm] = useState([]); //   

  //   
  const onSearch = async (data) => {
    const { search: keyword } = data; //  

    //    
    if (!keyword.trim()) {
      setTerm([]);
      return;
    }

    try {
      const response = await careData(); // API 
      console.log("API :", response);

      //       
      const results = response?.response?.body?.items?.item;

      //   
      if (Array.isArray(results)) {
        //  
        const filteredResults = results.filter(
          (item) =>
            item.cnterNm?.includes(keyword) || //  
            item.ctpvNm?.includes(keyword)    //  
        );
        setTerm(filteredResults); //   
      } else {
        setTerm([]); //     
      }
    } catch (error) {
      console.error("  :", error);
      setTerm([]); //    
    }
  };

  return (
    <Wrap>
      <Form onSubmit={handleSubmit(onSearch)}>
        <input
          {...register("search", {
            required: " .",
          })}
          type="text"
          placeholder="(ex:, , ) "
        />
      </Form>

      {term.length === 0 ? (
        <NoResultMessage>   .</NoResultMessage>
      ) : (
        <ConWrap>
          {term.map((data, index) => (
            <Link to = {`/detail/${data.cnterNm}`}>
              <Con key={index}>#{data.cnterNm}</Con>
            </Link>
          ))}
        </ConWrap>
      )}
    </Wrap>
  );
};

export default Search;